<template>
    <el-card>
        <h1>游戏</h1>
        <el-divider />
        <div class="gameList">
            <el-card
                shadow="hover"
                v-for="item in data"
                :key="item"
                @click="openGame(item)"
                body-style="padding:0"
            >
                <div class="cover" :style="{'background-image': `url(${item.cover})`}"></div>
                <p>{{item.name}}</p>
            </el-card>
        </div>
        <el-dialog
            v-model="dialogVisible"
            :before-close="handleClose"
            :show-close="false"
            top="10vh"
            :fullscreen="fullscreen"
        >
            <template #header="{ close }">
                <el-button-group size="small">
                    <el-button round title="浮窗">
                        <i class="ri-focus-line"></i>
                    </el-button>
                    <el-button round title="全屏" @click="fullscreen = !fullscreen">
                        <i :class="fullscreen ? 'ri-fullscreen-exit-line' : 'ri-fullscreen-line'"></i>
                    </el-button>
                    <el-button round title="关闭" @click="close">
                        <i class="ri-shut-down-line"></i>
                    </el-button>
                </el-button-group>
            </template>
            <div v-loading="loading">
                <iframe
                    ref="iframe"
                    :src="views.page"
                    frameborder="0"
                    scrolling="no"
                    :style="{ 
                    'min-height': views.height ? views.height:'70vh',
                    height: fullscreen ? '100vh' : 'auto',
                    width:'100%',
                    position: absolute,
                    top: 0,
                    left: 0,
                    right: 0,
                    bottom: 0,
                    margin: '0 -2px -4px 0'
                }"
                ></iframe>
            </div>
        </el-dialog>
    </el-card>
</template>
    
<script setup>
import { ref, reactive, onMounted, watch } from "vue";
import { ElMessageBox } from "element-plus";

const iframe = ref();
const fullscreen = ref(false);
const views = ref({});
const dialogVisible = ref(false);
const loading = ref(false);

const data = reactive([
    {
        name: "2048",
        cover: require("./cover/2048.webp"),
        page: "https://www.2048.org/",
        height: "670px"
    },
    {
        name: "Chrome Dino",
        cover: require("./cover/Dino.webp"),
        page: "https://spectregao.github.io/games/ChromeDino/",
        height: "60vh"
    },
    {
        name: "俄罗斯方块",
        cover: require("./cover/Tetris.webp"),
        page: "https://spectregao.github.io/games/Tetris/"
    },
    {
        name: "五子棋",
        cover: require("./cover/Gomoku.webp"),
        page: "https://spectregao.github.io/games/Gomoku/",
        height: "650px"
    }
]);

const handleClose = done => {
    ElMessageBox.confirm("是否确认关闭?", {
        confirmButtonText: "关闭",
        cancelButtonText: "取消",
        type: "warning"
    })
        .then(() => {
            done();
            views.value = {};
        })
        .catch(err => {});
};

const openGame = item => {
    if (iframe.value) {
        loading.value = true;
    }
    dialogVisible.value = true;
    views.value = item;
};
watch(
    views,
    val => {
        if (!iframe.value) return false;
        if (iframe.value.attachEvent) {
            iframe.value.attachEvent("onload", function() {
                loading.value = false;
                iframe.value.focus();
            });
        } else {
            iframe.value.onload = function() {
                loading.value = false;
                iframe.value.focus();
            };
        }
    },
    {
        deep: true
    }
);
</script>
<style>
.el-overlay-dialog,
.el-overlay-message-box {
    background: rgba(0, 0, 0, 0.4) !important;
}
</style>
<style  lang="less" scoped>
:deep(.el-dialog) {
    min-width: 630px !important;
    overflow: hidden;
    .el-dialog__header {
        position: absolute;
        right: 0;
        margin: 0;
        padding: 15px;
        .el-button-group > .el-button {
            background: rgba(255, 255, 255, 0.7);
            backdrop-filter: blur(15px);
        }
    }
    .el-dialog__body {
        padding: 0;
    }
}
.gameList {
    display: flex;
    flex: 1;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    .el-card {
        width: 200px;
        height: 250px;
        margin: 10px;
        cursor: pointer;
        user-select: none;
        &:hover {
            transform: scale(1.05);
        }
        &:active {
            transform: scale(0.95);
        }
        &::after {
            position: absolute;
            left: -80px;
            top: 0;
            content: "";
            width: 1000px;
            height: 50px;
            background-image: linear-gradient(
                0deg,
                rgba(0, 0, 0, 0),
                rgba(255, 255, 255, 0.4),
                rgba(0, 0, 0, 0)
            );
            transform: rotate(-45deg) translate(0, -360px);
        }
        &:hover::after {
            transition: 1s ease-in-out;
            transform: rotate(-45deg) translate(0, 60px);
        }
        .cover {
            width: 200px;
            height: 200px;
            background-size: cover;
            background-position: 50%;
            background-repeat: no-repeat;
        }
        p {
            text-align: center;
            line-height: 50px;
        }
    }
}
</style>